export default {
	props: {
		category: {
			type: String,
			default: 'discover'
		},
		curTab: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			categorys: {
				// discover: [ '阅读', '图文', '专题', '音乐', '影视', '小记', '作者' ],
				discover: [ '阅读', '图文', '专题', '小记', '作者', '音乐', '影视' ],
				series: [ 2020, 2019, 2018, 2017, 2016 ]
			},
			winHeight: "",
			currentTab: 0,
			scrollLeft: 0
		}
	},
	computed: {
		scrollContentCategory () {
			return this.categorys[this.category]
		}
	},
	created () {
		this.currentTab = this.curTab // 让父级 props 可修改
	},
	methods: {
		swichNav: function(e, index) {
			
			// cur 是现在的当前序号
			let cur
			if (index === undefined) {
				cur = +e.currentTarget.dataset.current
				this.$emit('narBarChange', cur) // 告诉父组件
			} else {
				cur = index
			}
			
			if (this.currentTab === cur) { return false }
			this.currentTab = cur
			
			// 让标签自动居中
			if (cur > 2) {
				this.scrollLeft = 300
			} else {
				this.scrollLeft = 0
			}
		},
		
		handleSearchTap () {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		},
		handleScroll () {}
	}
}
